import { styled } from "@rp/react/theme"

type UseStylesProps = {
  size: string
  hasChildren: boolean
}

const PREFIX = "HeaderButton"

export const classes = {
  headerButtonContainer: `${PREFIX}-headerButtonContainer`,
  rootButton: `${PREFIX}-rootButton`,
  startIcon: `${PREFIX}-startIcon`,
  hintText: `${PREFIX}-hintText`,
}

export const HeaderStyled = styled("div", {
  shouldForwardProp: prop => prop !== "hasChildren",
})<UseStylesProps>(({ theme, size, hasChildren }) => ({
  [`&.${classes.headerButtonContainer}`]: {
    display: "flex",
    alignItems: "center",
    justifyContent: "right",
  },
  [`& .${classes.rootButton}`]: {
    alignItems: "start",
    display: "flex",
    height: size === "large" ? 56 : 36,
    justifyContent: "center",
    minWidth: hasChildren ? "auto" : 32,
    paddingLeft: hasChildren ? "auto" : 18,
    width: hasChildren ? "auto" : 32,
    whiteSpace: "nowrap",
  },
  [`& .${classes.startIcon}`]: {
    "& > *:first-of-type": {
      fontSize: 24,
    },
  },
  [`& .${classes.hintText}`]: {
    color: theme.palette.common.white,
    opacity: 0.7,
    marginRight: 8,
    fontSize: ".75rem", // 12px
  },
}))
